<template>
  <div class="material">
    <router-view></router-view>
  </div>
</template>
<script>
import Popups from './compnents/pop-ups-model' // 模拟弹窗组件
import { popData, popMethod } from '@/until/adminMethods/pop'

export default {
  components: {
    Popups
  },
  data () {
    return {
      ...popData,

      tableData: [],
      companyInfo: {},
      companyRuleValidate: {},
      tableTitle: [
        {
          title: '公司编号',
          key: 'content'
        },
        {
          title: '公司名称',
          key: 'course',
          width: 100,
          align: 'center'
        },
        {
          title: '纳税人识别号',
