          title: '申报发送时间',
          slot: 'action',

          align: 'center'
        },
        {
          title: '缴款期限',
          slot: 'action',

          align: 'center'
        },
        {
          title: '所在地',
          slot: 'action',

          align: 'center'
        },
        {
          title: '年平均工资',
          slot: 'action',

          align: 'center'
        },
        {
          title: '月平均工资',
          slot: 'action',

          align: 'center'
        },
        {
          title: '有效期间起',
          slot: 'action',

          align: 'center'
        },
        {
          title: '有效期间止',
          slot: 'action',

          align: 'center'
        },

        {
          title: '月公积金减除上限',
          slot: 'action',
          align: 'center'
        },
        {
          title: '操作',
          slot: 'action',
          align: 'center'
        }]
    }
  },
  computed: {},
  methods: {
    ...popMethod

  }
}
</script>
<style>
  @import "../../static/css/admin.css";
</style>



